nav
{
    background: #333;
    color: white;
}
nav a
{
    color: white !important;
}
ul
{
    margin: 0;
    padding: 0;
}
ul li
{
    padding: 5px 15px;
}
ul#produitListes li
{
    cursor: pointer;
    transition: all 0.6s ease;
    font-weight: bolder;
    margin: 5px 0;
}
li.selected
{
    background-color: #333;
    color: white;
}
.card
{
    width: 15rem;
}
.card img
{
    height: 120px;
    object-fit: cover;
}


/* Partie modal */

.modal h5
{
    font-weight: bold;
}
.modal h5::first-letter
{
    text-transform: uppercase;
}
.modal img
{
    height: 100px;
    width: 150px;
    object-fit: cover;
}

#allProductAndModal input[type="checkbox"]
{
    display: none;
}

#allProductAndModal input[type="checkbox"]:checked + .modal
{
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
}

